@font-face {
  font-family: "icon";
  src: url("/assets/fonts/icon.eot");
  src: url("/assets/fonts/icon.eot?#iefix") format("embedded-opentype"), url("/assets/fonts/icon.woff") format("woff"), url("/assets/fonts/icon.ttf") format("truetype"), url("/assets/fonts/icon.svg#icon") format("svg");
  font-weight: normal;
  font-style: normal;
}


[data-icon]:before {
  font-family: "icon" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]:before, [class*=" icon-"]:before {
  font-family: "icon" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-facebook:before {
  content: "\66";
}

.icon-twitter:before {
  content: "\74";
}

.icon-pinterest:before {
  content: "\70";
}

.icon-google-plus:before {
  content: "\67";
}

.icon-comments:before {
  content: "\63";
}

.icon-toc:before {
  content: "\6c";
}

.icon-down:before {
  content: "\64";
}
