// Floating Social Share

#floatingSocialShare {
  position: relative;
  *, :after, :before {
    -webkit-box-sizing: content-box !important;
    -moz-box-sizing: content-box !important;
    box-sizing: content-box !important;
  }
  a {
    position: relative;
    width: 45px;
    height: 30px;
    line-height: 20px;
    padding: 8px 0;
    display: list-item;
    list-style-type: none;
    text-align: center;
    color: #FFF;
    outline: 0 !important;
    &:focus, &:hover {
      color: #FFF;
      width: 52px;
      opacity: .8;
      transition-duration: .3s;
      transition-property: right;
      transition-timing-function: ease-out;
    }
    &:active, &:focus, &:hover, &:visited {
      outline: 0 !important;
    }
  }
  svg {
    fill: #FFF;
    height: 30px;
  }
  a {
    &.without-counter svg {
      position: absolute;
      top: 50%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
      left: 0;
      right: 0;
      margin: auto;
      text-align: center;
    }
    &.with-counter-desktop {
      height: 38px;
    }
  }
  .shareCount {
    position: absolute;
    top: 36px;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    color: #FFF;
    font-size: 10px;
  }
  .content-left, .content-right, .top-left, .top-right {
    position: fixed;
    top: 25%;
    transform: translateZ(0);
    -webkit-transform: translateZ(0);
  }
  .top-left {
    left: 0;
  }
  .top-right {
    right: 0;
    direction: rtl;
  }
  .content-left, .content-right {
    margin: 0;
  }
  .mail {
    background-color: #D14836;
  }
  .facebook {
    background-color: #3B5998;
  }
  .google-plus {
    background-color: #DC4E41;
  }
  .linkedin {
    background-color: #0077B5;
  }
  .odnoklassniki {
    background-color: #F4731C;
  }
  .pinterest {
    background-color: #BD081C;
  }
  .reddit {
    background-color: #FF4500;
  }
  .stumbleupon {
    background-color: #EB4924;
  }
  .telegram {
    background-color: #2CA5E0;
  }
  .tumblr {
    background-color: #36465D;
  }
  .twitter {
    background-color: #1DA1F2;
  }
  .vk {
    background-color: #6383A8;
  }
  .whatsapp {
    background-color: #00E676;
  }
}

@media only screen and (max-width: 961px) {
  #floatingSocialShare {
    margin-top: 50px;
    .content-left, .content-right, .top-left, .top-right {
      top: auto;
      bottom: 0;
      margin: 0;
      right: 0;
      left: 0;
      width: 100%;
      z-index: 999;
    }
    a {
      position: relative;
      float: left;
      display: list-item;
      list-style-type: none;
      &:focus, &:hover {
        -moz-transition-property: none;
        -webkit-transition-property: none;
        -o-transition-property: none;
        transition-property: none;
      }
    }
    svg {
      height: 24px;
    }
    .shareCount {
      top: 30px;
    }
  }
}

//Tocbot Styling

.toc {
  overflow-y: auto;
  > .toc-list {
    overflow: hidden;
    position: relative;
    li {
      list-style: none;
    }
  }
}

.toc-list {
  margin: 0;
  padding-left: 10px;
}

a.toc-link {
  color: currentColor;
  height: 100%;
}

.is-collapsible {
  max-height: 1000px;
  overflow: hidden;
  transition: all 300ms ease-in-out;
}

.is-collapsed {
  max-height: 0;
}

.is-position-fixed {
  position: fixed !important;
  top: 0;
}

.is-active-link {
  font-weight: 700;
}

